import styled from "styled-components";
import { mobile } from "../responsive";
import { Link } from "react-router-dom";

const Container = styled.div`
  flex: 1;
  margin: 3px;
  height: 70vh;
  position: relative;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  ${mobile({ height: "20vh" })}
`;

const Info = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Button = styled.button`
    border:none;
    padding: 10px;
    background-color: white;
    cursor: pointer;
    font-weight: 600;
    font-size:20px
`;

const CategoryItem = ({ item }) => {
  return (
    <Container>
        <Link to={item.end} className="link">
      <Image src={item.img} />
      <Info>
        <Button style={{color: "#6e2f15", width:'80%'}}>{item.title}</Button>
      </Info>
      </Link>
    </Container>
  );
};

export default CategoryItem;